@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@300;400&display=swap');

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  font-family: 'Poppins', sans-serif;
}

.container {
  max-width: 500px;
  margin: 30px auto;
  overflow: auto;
  min-height: 300px;
  border: 1px solid steelblue;
  padding: 25px;
  border-radius: 5px;
  position: relative;
  background-color: #F4F6FA;
}

.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 20px;
}

.btn {
  display: inline-block;
  background: #000;
  color: #fff;
  border: none;
  padding: 10px 20px;
  margin: 5px;
  border-radius: 5px;
  cursor: pointer;
  text-decoration: none;
  font-size: 15px;
  font-family: inherit;
}

.btn:focus {
  outline: none;
}

.btn:active {
  transform: scale(0.98);
}

.btn-block {
  display: block;
  width: 100%;
}

.task {
  background: #f4f4f4;
  margin: 5px;
  padding: 10px 20px;
  cursor: pointer;
}

.task.reminder {
  border-left: 5px solid green;
}

.task h3 {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.add-form {
  margin-bottom: 40px;
}

.form-control {
  margin: 20px 0;
}

.form-control label {
  display: block;
}

.form-control input {
  width: 100%;
  min-height: 45px;
  margin: 5px;
  padding: 6px 12px;
  font-size: 17px;
  border: none;
  border-radius: 4px;
}

.form-control-check {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.form-control-check label {
  flex: 1;
}

.form-control-check input {
  flex: 2;
  height: 20px;
}

footer {
  margin-top: 30px;
  text-align: center;
}

.about-page{
    text-align: center;
    padding: 25px 0;
}

.container .loader{
  position: absolute;
  top:0;
  left:0;
  right:0;
  bottom: 0;
  background-color: #fff;
  display: flex;
  justify-content: center;
  align-items: center;
}

.blink_me {
  animation: blinker 1s linear infinite;
}

@keyframes blinker {  
  50% { opacity: 0; }
}

.result-card{
  background-color:#fff;
  padding: 15px 25px;
  border-radius: 6px;
  box-shadow: 0 .125rem .25rem rgba(0,0,0,.075)!important;
  margin-bottom: 15px;
}

.result-card .title{
    margin-bottom: 15px;
    border-bottom: 1px solid #E8E8E8;
    padding-bottom: 15px;
}

.result-card .fileName{
  margin-bottom: 20px;
}

.d-flex{display: flex;}
.justify-content-between{
  justify-content: space-between;
}
.justify-content-start{
  justify-content: start;
}
.align-items-center{
  align-items: center;
}

.w-50{
  width:50%;
}

.fileInfo .label{
  font-size: 14px;
}


.all-records-container{
  background-color: white;
  padding: 12px 15px;
  border-radius: 6px;
}

.all-records-container .title{
  margin-bottom: 20px;
}

.all-records-container .record .initial{
    width: 35px;
    height: 35px;
    border-radius: 100%;
    background: #f8371b;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-right: 12px;
    color:#fff;
}

.all-records-container .record .docName{
    padding: 10px 5px;
    border-bottom: 1px solid #E8E8E8;
    width: calc(100% - 47px);
    text-transform: capitalize;
}

.all-records-container .record .parent-div{
  width: calc(100% - 24px);
}

.all-records-container .record:last-of-type .docName{
  border-bottom: none;
}

.close:hover{
  cursor: pointer;
}

.close svg line{
  stroke:#F8371B;
}

.record-container .body{
  padding: 12px;
  margin-left: 35px;
  font-size: 14px;
}

.record-container{
  transition: padding 0.3s;
}

.record-container span{
  transition: transform 0.3s;
}

.record-container.active{
    padding: 5px;
    box-shadow: 0 .125rem .25rem rgba(0,0,0,.075)!important;
    border-radius: 4px;
    margin-bottom: 12px;
}

.record-container.active span{
  transform: rotate(180deg);
}